<style>
  .error-t-w {
    line-height: 32px;
    margin-bottom: 10px;
    background-color: #F4ACAE;
    padding-left: 10px;
    color: #555;
  }
  .c-btn-w {
    line-height: 32px;
    padding: 0 10px;
    float: right;
    cursor: pointer;
  }

  .d-j-w {
    display: flex;
    flex-wrap:nowrap;
    flex-direction: row;
  }
  
  .detail-j-w {
    line-height: 23px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .s-d-m-w {
    flex: 0 0 76px;
    width: 76px;
    text-align: center;
    line-height: 23px;
    cursor: pointer;
    color: #2077FF;
  }
  .s-d-m-w:hover {
    color: #66b1ff;
  }

  .dailog-e-m-d-w {
    max-height: 330px;
    overflow: auto;
  }
  
  .dailog-e-m-d-w pre {
    white-space:pre-wrap;
    word-wrap:break-word;
  }

  .error-item-w {
    line-height: 30px;
  }
  .i-th-w {
    display: inline-block;
    width: 100px;
  }
  .d-m-w {
    color: red;
  }

  .sp-handel-w {
    padding-left: 18px;
    padding-bottom: 10px;
    border-bottom: 1px solid #ececec;
  }
</style>

<template>
  <div>
    <div class="error-t-w" v-show='isShowTip'>
      <i class="el-icon-warning"></i>注意：请修复问题后再补单，请按门店检索后全选批量补单，或按三方平台检索后全选批量补单
      <span class="c-btn-w"><i class="el-icon-close" @click="isShowTipHandel"></i></span>
    </div>
    <!-- 操作框 -->
    <div class="search-form-wraper">
      <el-form :model="searchContent" label-position="top">
        <el-form-item label="三方门店ID">
          <el-input class="search-input" v-model.trim="searchContent.shopId" @keyup.enter.native="doSearchHandel(true)" placeholder="请输入三方门店ID" size="small" clearable></el-input>
        </el-form-item>
        <el-form-item label="门店名称">
          <el-input class="search-input" v-model.trim="searchContent.shopName" @keyup.enter.native="doSearchHandel(true)" placeholder="请输入门店名称" size="small" clearable></el-input>
        </el-form-item>

        <el-form-item label="三方平台">
          <el-select class="search-input" v-model="searchContent.platformId" filterable placeholder="请选择三方平台" clearable size="small" @change="doSearchHandel(true)">
            <el-option
              v-for="item in platformIdOptions"
              :key="item.platformId"
              :label="item.name"
              :value="item.platformId">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-- 搜索结果 -->
    <div class="table-wraper">
      <div class="sp-handel-w">
        <el-button size="mini" class="sup-mini" :disabled = "this.orderIdList.length === 0" @click="makeUpAllOrdersHandel"  icon="el-icon-edit">批量补单</el-button>
      </div>
      <el-table
        ref="multipleTable"
        class="fixed-height"
        v-loading="loading"
        :data="tableData"
        empty-text="没有查询到您想要的数据"
        @select="handleSelect"
        @select-all="handleSelectAll"
        style="width:100%;"
        size="small">
        <el-table-column
          type="selection"
          label="全选"
          align="center">
        </el-table-column>
        <el-table-column min-width="100px" prop="platformName" label="三方平台"></el-table-column>
        <el-table-column min-width="178px" prop="orderId" label="订单号"></el-table-column>
        <el-table-column min-width="110px" :formatter="nullTreating" prop="shopId" label="三方门店ID"></el-table-column>
        <el-table-column min-width="140px" prop="shopName" label="门店名称"></el-table-column>
        <el-table-column min-width="220px" label="详细信息">
          <template slot-scope="scope">
            <div class="d-j-w">
              <div class="detail-j-w">{{scope.row.detailJson}}</div>
              <div class="s-d-m-w" @click="showErrDetail(scope.row.detailJson)">查看信息</div>
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="150px" prop="errMsg" label="接入失败原因"></el-table-column>
        <el-table-column
          fixed="right"
          label="操作"
          width="100">
          <template slot-scope="scope" size="small">
            <el-button @click="makeUpOrders(scope.row)" size="mini" icon="el-icon-edit">补单</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页条 -->
      <el-pagination
        v-show="tableData.length>0"
        class="tc mt20"
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="pageNum"
        :page-sizes="pageSizes"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>

    <!-- 展示失败信息详情的弹框 -->
    <el-dialog title="详细信息" :visible.sync="orderErrorMesgVisible" width='50%'>
      <div class="dailog-e-m-d-w">
        <pre>
          {{errDetailMesg}}
        </pre>
      </div>

      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="orderErrorMesgVisible = false" size="mini">关 闭</el-button>
      </div>
    </el-dialog>

    <!-- 补单成功信息弹框 -->
    <el-dialog :title="makeUpOrdersTitle" :visible.sync="makeUpOrdersDialogShow" width='400px'>
      <div class="dailog-e-m-d-w">
        {{makeUpOrdersResDetail}}
      </div>

      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="makeUpOrdersDialogShow = false" size="mini">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Storage from '@/utils/localStorage'
import config from '@/utils/config'
import * as utils from '@/utils/utils'
// import RegionCityStore from '@/components/Common/StoreCommon/RegionCityStore';
const orderEcptionMgtUrl = {
  // 获取门店列表接口
  search: '/plateform/a/store/condition/search',
  // 商品管理-获取平台列表
  getPlatformList: '/plateform/a/product/getPlatformList',
  // 异常订单查询接口
  orderQuery: '/plateform/a/order/search/replenish',
  // 手动补单
  replenish: '/plateform/a/order/replenish'
}
export default {
  data() {
    return {
      pageSizes: config.pageSizes20,
      isShowTip: true, // 是否显示操作提示，默认显示f
      orderErrorMesgVisible: false, // 是否展示失败原因详细信息弹框
      makeUpOrdersDialogShow: false, // 补单成功弹窗
      makeUpOrdersTitle: '', // 补单成功弹窗title
      makeUpOrdersResDetail: '',
      loading: false,
      pageNum: 1,
      pageSize: 20,
      total: 0,
      tableData: [],
      searchContent: {
        regionCode: Storage.get('regionCode') || '',
        platformId: 0, // 平台ID
        shopId: '', // 三方门店ID
        shopName: '', // 三方门店名称
      },
      orderIdList: [], // 订单号列表
      orderIdListTemp: [], // 订单号列表临时缓存用
      singleOrderId: [], // 点击的那个补单
      // 平台id
      platformIdOptions: [
        {
          "name": "全部",
          "platformId": 0
        }
      ],
      errDetailMesg: null // 错误信息详情

    }
  },
  // components: {
  //   "region-city-store": RegionCityStore
  // },
  created() {
    // 获取订单来源
    this.getOrderOrigion()
  },
  mounted() {
    // 获取表格中的数据
    this.doSearchHandel(true)
  },
  methods: {
    /**
     * 数据处理
     */
    nullTreating (row, column, cellValue) {
      if(cellValue == null) {
        cellValue = '-'
      }
      return cellValue
    },
    /**
     * [isShowTipHandel 是否显示操作提示事件]
     * @return {Boolean} [description]
     */
    isShowTipHandel() {
      this.isShowTip = false
    },
    /**
     * [getOrderOrigion 商品管理-获取平台列表]
     * @return {[type]} [description]
     */
    getOrderOrigion() {
      this.$http.get(orderEcptionMgtUrl.getPlatformList, {DIFFCITY: true}).then(res => {
        if(res.code === 0) {
          if(res.data && res.data.length) {
            let platformItem = {
              "name": "全部",
              "platformId": 0
            }
            res.data.unshift(platformItem)
            this.platformIdOptions = res.data
          } 
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        console.log("提示(手动补单-获取平台列表):", e.message)
      })
    },
    /**
     * [changeSize description]
     * @param    {[type]}                 value [description]
     * @return   {[type]}                       [description]
     */
    changeSize (value) {
      this.pageSize = value
      this.doSearchHandel()
    },
    /**
     * [changeCurrent 点击分页条]
     * @param    {[type]}                 value [当前页]
     * @return   {[type]}                       [description]
     */
    changeCurrent (value) {
      this.pageNum = value
      this.doSearchHandel()
    },
    /**
     * [doSearchHandel 查询]
     * @return {[type]} [description]
     */
    doSearchHandel(_param) {
      this.orderIdList = []
      this.loading = true
      if(_param === true) {
        this.pageNum = 1
      }
      this.$http.post(orderEcptionMgtUrl.orderQuery, {...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize, DIFFCITY: true }).then(res => {
        this.loading = false
        if(res.code === 0) {
          if(res.data && res.data.result && res.data.result.length) {
            for(let item of res.data.result) {
              if(!item.shopName || item.shopName === '') {
                item.shopName = '暂无'
              }
            }
            this.tableData = res.data.result
          }else {
            this.tableData = []
          }

          if(this.pageNum == 1) {
            this.total = res.data.total
          }
          return 
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.loading = false
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * [showErrDetail 展示错误信息详情]
     * @return {[type]} [description]
     */
    showErrDetail(_value) {
      this.errDetailMesg = JSON.parse(_value)
      this.orderErrorMesgVisible = true
    },
    /**
     * [makeUpOrders 补单]
     * @param  {[type]} _row [行信息]
     * @return {[type]}        [description]
     */
    makeUpOrders(_row) {
      this.singleOrderId = []
      this.singleOrderId.push(_row.orderId)

      this.$http.post(orderEcptionMgtUrl.replenish, {orderIdList: this.singleOrderId, DIFFCITY: true}).then(res => {
        this.loading = false
        if(res.code === 0) {
          this.$message.success("Oh yeah，补单成功！！")
          this.doSearchHandel()
          return 
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.loading = false
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })

    },
    /**
     * [makeUpAllOrdersHandel 处理全选本页事件]
     * @return {[type]} [description]
     */
    makeUpAllOrdersHandel() {
      utils.showLoading()
      this.$http.post(orderEcptionMgtUrl.replenish, {orderIdList: this.orderIdList, DIFFCITY: true}).then(res => {
        utils.clearLoading()
        if(res.code === 0) {
          // 成功
          this.makeUpOrdersDialogShow = true
          this.makeUpOrdersTitle = "补单成功"
          this.makeUpOrdersResDetail = "Oh yeah，补单成功！"
          this.doSearchHandel()
          return 
        }else if(res.code === 1) {
          // 失败
          this.makeUpOrdersDialogShow = true
          this.makeUpOrdersTitle = "补单失败"
          this.makeUpOrdersResDetail = "Sorry，补单失败，请在列表中查看失败原因并做相应处理！"
          return 
        }else if(res.code === 2) {
          // 部分成功
          this.makeUpOrdersDialogShow = true
          this.makeUpOrdersTitle = "补单部分成功"
          let errOrderList = ''
          if(res.data && res.data.length > 0) {
            errOrderList = res.data.join(',')
          }
          this.makeUpOrdersResDetail = "批量处理补单部分成功！失败订单编号为：" + errOrderList + "，请在列表中查看失败原因并做相应处理！"
          this.doSearchHandel()
          return 
        }
        throw new Error(res.msg)
      }).catch(e => {
        utils.clearLoading()
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * [handleSelect 选择某一个]
     * @return {[type]} [description]
     */
    handleSelect(val, row) {
      // 临时缓存使用
      let orderIdListTemp = [];

      if(utils.indexInArr(this.orderIdList, row.orderId) > -1) {
        this.orderIdList = utils.removeElFromArr(this.orderIdList, row.orderId)
        return
      }else {
        orderIdListTemp.push(row.orderId)
      }

      // 获取去重后的上传的参数
      this.orderIdListTemp = [...orderIdListTemp, ...this.orderIdList]
      this.orderIdList = Array.from(new Set(this.orderIdListTemp))

    },
    /**
     * [handleSelectAll 选择多个]
     * @return {[type]} [description]
     */
    handleSelectAll(val) {
      if(val.length > 0) {        
        // 全选
        for(let item of val) {
          this.orderIdList.push(item.orderId)
        }
        // 去重
        this.orderIdList = Array.from(new Set(this.orderIdList))

      }else { 
        // 全不选 此时val.length<=0
        // 1.获取当前table中每一条的id
        let orderIdList = []
        for(let i of this.tableData) {
          orderIdList.push(i.orderId)
        }
        // 2.从要传递的参数中去掉这些id
        this.orderIdList = Array.from(new Set([...this.orderIdList].filter(j => !new Set(orderIdList).has(j))))
      }
      
    }
  }
}
</script>


